import React from "react"

import Card from "react-bootstrap/Card"

export default function Blog(props){
    return (
        
        <Card>
          <Card.Header>{props.date}</Card.Header>    
          <Card.Body>
          <Card.Title>{props.title}</Card.Title>
            {props.body}
          </Card.Body>
        </Card>
        
    )
}